.review-listing-nav {
    width: 810px;
    margin-bottom: 24px;
    display: flex;
    border-bottom: 2px solid rgba(34, 34, 34, 0.15);
}
.review-listing-nav-button {
    white-space: normal;
    background: none;
    border: none;
    color: inherit;
    font: inherit;
    text-align: left;
    outline: none;
    text-decoration: none;
    -webkit-tap-highlight-color: transparent;
    -webkit-appearance: none;
    display: flex;
    align-items: center;

    margin: 12px 12px 12px 0;
    position: relative;
    font-size: 16px;
}
.review-listing-nav-button::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: calc(100%);
    height: calc(100% + 24px);
    background: transparent;
    z-index: -1;
}
.review-listing-nav-button::after {
    width: 100%;
    border-bottom-color: #222222;
    bottom: -14px;
    content: '';
    display: inline-block;
    position: absolute;
    width: 0;
    border-bottom: 2px solid rgba(34, 34, 34, 0.5);
    left: 50%;
    bottom: -2px;
    transform: translateX(-50%);
    transition: width 200ms cubic-bezier(0.54, 0, 0.54, 1);
}
.review-listing-nav-button-content {
    background: #eaeaea;
    margin-left: 12px;
    border-radius: 15px;
    color: inherit;
    display: inline-block;
    word-break: break-word;
    line-height: 1;
    font-size: 13px;
    font-weight: normal;
    padding: 6px 9px;
    min-width: 25px;
}
